.Carousel {
  display: flex;
  flex-direction: column;
  align-items: center;

  &-container {
    overflow: hidden;
  }

  &__list {
    list-style: none;
    display: flex;
    flex-direction: row;
    padding: 0;
  }
}

button {
  width: 200px;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.inputs {
  display: flex;
  gap: 10px;
}